import React from 'react'

export default function Temperature({temp, onTempChange}) {
    return (
        <div className="container">
            <h1 className="indigo-text text-darken-2">Temperature Conversion</h1>
            <div className="row">
                <div className="col s12 m6">
                    <div className="input-field center">
                        <label htmlFor="cel">Temp in Celius</label>
                        <input type="number" id="cel" className="validate" value={temp.cel} onChange={onTempChange}/>
                    </div>
                </div>
                <div className="col s12 m6">
                    <div className="input-field center">
                        <label htmlFor="fah">Temp in Fahrenhet</label>
                        <input type="number" id="fah" className="validate" value={temp.fah} onChange={onTempChange}/>
                    </div>
                </div>
            </div>
            <div className="container">
                {
                    temp.cel !== null && temp.cel > 100?
                    <p className="center red">The water boils in the given tempertaure</p> : 
                    <p className="center yellow">The water doesn't boils in the given tempertaure</p>
                }
            </div>
            
        </div>
    )
}
