import React from 'react';
import { NavLink } from 'react-router-dom';

export default function Navbar() {
    return (
        <div>
            <nav className="nav-wrapper indigo darken-1">
                <div className="container">
                    <a href="/" className="brand-logo">React JS</a>
                    <a href="/" data-target="mobile-links" className="sidenav-trigger">
                        <i className="material-icons">menu</i>
                    </a>
                    <ul className="right hide-on-med-and-down">
                        <li><NavLink to="/">Home</NavLink></li>
                        <li><NavLink to="/todo">Todo</NavLink></li>
                        <li><NavLink to="/temperature">Temperature</NavLink></li>
                        <li><NavLink to="/ttt">tictactoe</NavLink></li>
                    </ul>
                </div>
            </nav>
            <ul className="sidenav" id="mobile-links">
                <li><NavLink to="/">Home</NavLink></li>
                <li><NavLink to="/todo">Todo</NavLink></li>
                <li><NavLink to="/temperature">Temperature</NavLink></li>
                <li><NavLink to="/ttt">tictactoe</NavLink></li>
            </ul>
        </div>
    )
}
