import React, { Component } from 'react'

export default class TTT extends Component {
    state = {
        board : [
            [null, null, null],
            [null, null, null],
            [null, null, null]
        ],
        turn: 0,
        win: null,
        xwins: 0,
        owins: 0
    }

    plot = (e) => {
        let n = parseInt(e.target.id);
        let b = this.state.board;
        if (this.state.turn %2 === 0)
            b[parseInt(n/3)][parseInt(n%3)] = 'x';
        else
            b[parseInt(n/3)][parseInt(n%3)] = 'o';

        let turn = this.state.turn+1;
        this.setState({
            board: b,
            turn
        });
        this.handleWin();
    }

    handleWin = () => {
        let b = this.state.board;
        let win = null;
        for( let i = 0; i< 3;i++){
            let sum1 =0, sum2 =0, sum3 =0, sum4 = 0;
            for(let j = 0 ; j < 3 ; j++){
                if(b[i][j] === 'x')
                    sum1++;
                if(b[j][i] === 'x')
                    sum2++;
                if(b[i][j] === 'o')
                    sum3++;
                if(b[j][i] === 'o')
                    sum4++;
            }
            if ( sum1 === 3 || sum2 === 3)
                win = 'x';
            if ( sum3 === 3 || sum4 === 3)
                win = 'o';
        }
        if ( win !== 'o' || win !== 'x'){
            console.log("test");
            let sum1 = 0, sum2 = 0, sum3 = 0, sum4 = 0;
            for ( let i = 0; i<3;i++){
                if(b[i][i] === 'x')
                    sum1++;
                if(b[i][i] === 'o')
                    sum2++;
                if(b[i][2-i] === 'x')
                    sum3++;
                if(b[i][2-i] === 'o')
                    sum4++;
            }
            if ( sum1 === 3 || sum2 === 3)
                win = 'x';
            if ( sum3 === 3 || sum4 === 3)
                win = 'o';

        }

        if(win === 'x'){
            let xwins = this.state.xwins+1;
            this.setState({
                xwins
            })
            setTimeout(()=>{
                this.reset()
            }, 3000)
        }
        if(win === 'o'){
            let owins = this.state.owins+1;
            this.setState({
                owins
            })
            setTimeout(()=>{
                this.reset()
            }, 3000)
        }
        this.setState({
            win
        })
        
    }

    reset = (e) => {
        let board = [
            [null, null, null],
            [null, null, null],
            [null, null, null]
        ];

        this.setState({
            turn: 0,
            win: null,
            board
        })
    }

    render() {
        return (
            <div>
                <div className="container section">
                    <h2 className="indigo-text text-darken-3 center-align">Welcome to Tic Tac Toe</h2>
                    {
                        (this.state.win)?(
                                <div>
                                    <h4 className="center-align green-text">{ this.state.win } won the match</h4>
                                    <p className="center-align green-text darken-4">(Match restart automatically in 3sec :))</p>
                                </div>
                        ): null
                    }
                    <div className="row">
                        <div className="col s6">
                            <h5 className="indigo-text right">X-Won: {this.state.xwins} </h5>
                        </div>
                        <div className="col s6">
                            <h5 className="indigo-text left">O-Won: {this.state.owins} </h5>
                        </div>
                    </div>
                    <table>
                        <tbody>
                            <tr>
                                <td id="0" onClick={this.plot}>{this.state.board[0][0]}</td>
                                <td className="vert"id="1" onClick={this.plot}>{this.state.board[0][1]}</td>
                                <td id="2" onClick={this.plot}>{this.state.board[0][2]}</td>
                            </tr>
                            <tr>
                                <td id="3" onClick={this.plot}>{this.state.board[1][0]}</td>
                                <td className="vert" id="4" onClick={this.plot}>{this.state.board[1][1]}</td>
                                <td id="5" onClick={this.plot}>{this.state.board[1][2]}</td>
                            </tr>
                            <tr>
                                <td id="6" onClick={this.plot}>{this.state.board[2][0]}</td>
                                <td className="vert"id="7" onClick={this.plot}>{this.state.board[2][1]}</td>
                                <td id="8" onClick={this.plot}>{this.state.board[2][2]}</td>
                            </tr>
                        </tbody>
                        
                    </table>
                    <div className="center-align padd">
                        <button className="btn red" onClick={this.reset}>Reset</button>
                    </div>
                </div>
                
            </div>
        )
    }
}
