import React, { Component } from 'react';
import Navbar from './components/navbar';
import { BrowserRouter, Route } from 'react-router-dom';
import Home from './components/home';
import Todo from './components/todo';
import TTT from './components/ttt';
import Temperature from './components/temperature';


class App extends Component {
  state = {
    todoList: {
      todos: [{id: 1, todo:"buy milk"}]
    },
    temp: {
      cel: null,
      fah: null
    }
  }

  handleSubmit = (todo) => {
      let t = {id: Math.random(), todo: todo};
      let todos = this.state.todoList.todos;
      todos = [...todos, t];
      let todoList = {todos};
      this.setState({
          todoList
      });
  }

  handleDelete = (id) => {
      let todos = this.state.todoList.todos.filter(todo => todo.id !== id);
      todos = {todos};
      this.setState({
          todoList: todos
      })
  }

  onTempChange = (e) =>{
    let cel, fah;
    if(e.target.id === "cel"){
      cel = parseFloat(e.target.value);
      fah = 9*cel/5 + 32;
    } else {
      fah = parseFloat(e.target.value);
      cel = (fah - 32)*5/9;
    }
    let temp = {fah: fah, cel: cel};
    this.setState({
      temp
    })
  }

  render(){
    return (
      <div className="App">
        <BrowserRouter>
          <Navbar />
          <Route exact path="/">
            <Home todolen = {this.state.todoList.todos.length} temp = {this.state.temp}/>
          </Route>
          <Route path="/todo">
            <Todo todos={this.state.todoList.todos} handleDelete={this.handleDelete} handleSubmit={this.handleSubmit}/>
          </Route>
          <Route path="/temperature">
            <Temperature temp={this.state.temp} onTempChange={this.onTempChange}/>
          </Route>
          <Route path="/ttt" component ={ TTT }/>
        </BrowserRouter>
      </div>
    );
  } 
}

export default App;
