import React, { Component } from 'react'

export default class Todo extends Component {
    state = {
        todo: ''
    }

    handleChange = (e) => {
        let val = e.target.value;
        this.setState({
            todo: val
        });
    }
    tempsub = (e) => {
        e.preventDefault();
        this.props.handleSubmit(this.state.todo);
        this.setState({todo:''})
    }
    render() {
        let todoList = this.props.todos.length === 0 
        ?
        ( <li className="collection-item">No Todo, please add your todo yay!</li> )
        :
        this.props.todos.map( todo => (
            <li className="collection-item" key={todo.id}>
                {todo.todo} 
                <a href="" className="right">
                    <i className="material-icons" onClick={(e) => {this.props.handleDelete(todo.id)}} id={todo.id}>clear</i>
                </a>
            </li>
        ))
        return (
            <div>
                <div className="container section">
                    <h2 className="blue-text lighten-2 center-align">Add Todo's</h2>
                    <form onSubmit = {this.tempsub}>
                        <div className="row">
                            <div className="input-field col s12 l7 ">
                                <label htmlFor="todo">Add Todo...</label>
                                <input type="text" className="validate" id="todo" onChange={this.handleChange} value={this.state.todo}/>
                            </div>
                            <div className="col s12 l4 offset-1 center">
                                <button href="#" className="btn center indigo waves-effect waves-light" type="submit">Add Todo to list  <i className="material-icons right">add_box</i></button>
                            </div>
                        </div>
                    </form>
                    <ul className="collection">
                        {
                            todoList
                        }
                    </ul>
                </div>
            </div>
        )
    }
}
